import React, { Component } from 'react';
import { List, Button, Flex } from 'antd-mobile';
import PropTypes from 'prop-types';

const Item = List.Item;

class LockDesc extends Component {
  state = {
    disabled: false,
  };

  listConfig = [
    {
      label:
        '遇到帐号被盗等紧急情况，可以通过锁定保护来防止坏人窃取隐私、盗取资金、发送垃圾信息等',
      icon: <i className="iconfont iconfont-sm iconfont-lock-no1" />,
      key: '1',
    },
    {
      label:
        '帐号锁定成功后，已经登录的所有设备会被踢下线。锁定期间所有登录方式都不允许登录',
      icon: <i className="iconfont iconfont-sm iconfont-lock-no2" />,
      key: '2',
    },
    {
      label: '只有锁定保护解除后才能正常登录帐号',
      icon: <i className="iconfont iconfont-sm iconfont-lock-no3" />,
      key: '3',
    },
  ];

  onClick = () => {
    this.props.onClick();
  };

  render() {
    return (
      <div className="all">
        <div className="i-body">
          <div className="i-title">
            <i className="iconfont iconfont-lg iconfont-lock" />
            <div className="title">开启锁定保护</div>
          </div>
          <div
            className="i-border"
            style={{ margin: '0.4rem 0.32rem 0.16rem' }}
          />
          <div className="body-content">
            {this.listConfig.map(item => (
              <Item
                style={{ padding: '0.31rem 0' }}
                key={item.key}
                align="top"
                thumb={item.icon}
                multipleLine
                wrap
              >
                {item.label}
              </Item>
            ))}
          </div>
        </div>
        <div className="i-footer">
          <Flex>
            <Flex.Item>
              <Button
                type="warning"
                className="footer-button"
                onClick={this.onClick}
              >
                开启锁定保护
              </Button>
            </Flex.Item>
          </Flex>
        </div>
      </div>
    );
  }
}

export default LockDesc;

LockDesc.propTypes = {
  onClick: PropTypes.func,
};

LockDesc.defaultProps = {
  onClick: () => {},
};



