export const getNavBarTitle = (steps, type) => {
  let navBarTitle = '';
  switch (steps) {
    case 'LockId':
      navBarTitle =
        type === 'LOCK'
          ? '请输入要锁定保护的抖音号'
          : '请输入要解除锁定的抖音号';
      break;
    case 'LockMobile':
      navBarTitle = '请输入手机号';
      break;
    default:
      break;
  }
  return navBarTitle;
};

export default {};



